.header {
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.15);
    position: relative;
    z-index: 11;
    &__content {
        position: relative;
        display: flex;
        justify-content: space-between;
        padding: 10px 0;
        &::after {
            position: absolute;
            left: 50%;
            transform: translate(-50%);
            bottom: 0;
            opacity: 0.3;
            width: 100vw;
            height: 1px;
            content: "";
            background: #c4c4c4;
        }
    }

    &__left {
        display: flex;
        align-items: center;
    }

    &__logo-text {
        margin-left: 20px;
        
    }

    &__name {
        font-family: "PT Serif";
        color: black;
        font-size: 20px;
        font-weight: 700;
        line-height: 27px;
    }

    &__subname {
        font-family: "Roboto";
        font-size: 18px;
        font-weight: 400;
        line-height: 21px;
        color: black;
    }

    &__right {
        display: flex;
        align-items: center;
    }

    &__num,
    &__mail {
        display: flex;
        align-items: center;
    }
    &__num-text,
    &__mail-text {
        color: $white;
        font-family: "Roboto";
        font-size: 18px;
        font-weight: 700;
        line-height: 23px;
        margin-left: 13px;
        color: black;
    }
    &__num {
        margin-right: 33px;
    }
    &__mail {
        margin-right: 45px;
    }
    &__btn {
        height: 44px;
        width: 186px;
        display: flex;
        justify-content: center;
        align-items: center;

        font-family: Roboto;
        font-size: 16px;
        font-weight: 400;
        line-height: 19px;
    }

    &__nav {
        display: flex;
        align-items: center;
        margin-top: 20px;
        padding-bottom: 15px;
    }

    &__nav-items {
        font-family: Roboto;
        font-size: 16px;
        font-weight: 400;
        line-height: 19px;
        color: black;
        position: relative;
        & + & {
            margin-left: 32px;
        }
        &::after {
            position: absolute;
            height: 2px;
            width: 100%;
            content: "";
            bottom: -6px;
            left: 0;
            opacity: 0;
            background-color: #000;
            transition: opacity 0.1s linear;
        }
        &:hover {
            &::after {
                opacity: 1;
            }
        }
    }
}
