.toursbase-content {
    margin-top: 80px;
    margin-bottom: 80px;
    &__info {
        font-family: Roboto;
        font-size: 20px;
        font-weight: 400;
        line-height: 28px;
        color: black;
        margin-bottom: 50px;
    }

    &__link {
        color: $blue-text;
        text-decoration: underline;
        transition: color 0.1s linear;
        &:hover {
            color: $active-link;
        }
    }
    &__items {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        background: $bg-light;
        column-gap: 32px;
        border-radius: 6px;
        overflow: auto;
        &+&{
            margin-top: 32px;
        }
    }

    &__middle {
        margin-top: 21px;
    }

    &__middle-title {
        color: black;
        font-family: Roboto;
        font-size: 24px;
        font-weight: 700;
        line-height: 28px;
        margin-bottom: 17px;
    }

    &__middle-text {
        color: $main;
        font-family: Roboto;
        font-size: 16px;
        font-weight: 400;
        line-height: 21px;
        margin-bottom: 18px;
    }

    &__middle-btn {
        width: 156px;
        height: 44px;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 16px;
        font-weight: 400;
        line-height: 19px;
    }

    &__end {
        margin-top: 21px;
        margin-right: 16px;
        position: relative;
        &::after{
            position: absolute;
            content: '';
            height: calc(100% - 20px);
            width: 2px;
            background-color: $divid;
            top: 0px;
            left: -16px;
        }
    }

    &__end-title {
        color: $text-help;
        font-family: Roboto;
        font-size: 20px;
        font-weight: 500;
        line-height: 23px;
        margin-bottom: 17px;
    }
    &__end-list {
        overflow-y: auto;
        display: flex;
        flex-direction: column;
        max-height: 265px;
        &::-webkit-scrollbar {
            width: 8px;
            background-color: $divid;
            border-radius: 4px;
        }
        &::-webkit-scrollbar-thumb {
            border-radius: 4px;
            background-color: $text-help;
        }
    }
    &__end-link {
        color: $blue-text;
        font-family: Roboto;
        font-size: 16px;
        font-weight: 400;
        line-height: 21px;
        margin-bottom: 10px;
        &:hover{
            text-decoration: underline;
        }
    }
}